import React, { Component } from 'react';
import './Components.css';
import image from './images/image.png';

class Header extends Component {
  render() {
    return(
      <div>
        <header>
          <div className="content-wrap">
            <img className="profile-image col-narrow" src={image} alt="Me" />
            <div className="col-wide">
              <h1>Apostolos Lalos</h1>
              <h2>Computer and Software Engineer</h2>
              <p>High motivated, passionate and curious student pursuing a MSc in Computer Science at the Technical
    University of Denmark (DTU) with specialization in software engineering. I am driven by challenges
    and I am always pushing myself out of my comfort zone. I developed
    a combination of new developing techniques and communication skills by working with cross-cultural
    teams in an international environment.</p>
            </div>
          </div>

        </header>
      </div>
    );
  }

}

export default Header;
